import React from "react";
import o from "react-hyperscript";
import { Redirect } from "react-router-dom";
import { connect } from 'react-redux';
import axios from "axios";
import {List, ListItem} from 'material-ui/List';
import Avatar from "material-ui/Avatar";
import TextField from "material-ui/TextField";
import Subheader from "material-ui/Subheader";
import SelectField from 'material-ui/SelectField';
import FloatingActionButton from 'material-ui/FloatingActionButton';
import RaisedButton from 'material-ui/RaisedButton';
import ContentAdd from 'material-ui/svg-icons/content/add';
import MenuItem from 'material-ui/MenuItem';
import Checkbox from 'material-ui/Checkbox';

import {animalAdder} from "./animalEdit";
import {fetchAnimals} from "./actions.js";

import store from "./store";


const fabStyle = {
    margin: 0,
    top: 'auto',
    right: 20,
    bottom: 20,
    left: 'auto',
    position: 'fixed'
};


export class AnimalEdit extends React.Component {
    state = {
	is_lactating: false,
	breeds: []
    }

    constructor (props) {
	super(props);

	this.handleChange = this.handleChange.bind(this);
	this.handleSubmit = this.handleSubmit.bind(this);
    }

    componentDidMount () {
	axios.get("/api/breed/").then((res) => {
	    this.setState({breeds: res.data});
	});
    }

    handleChange = (event, index, animalType) => this.setState({animalType});

    handleSubmit = (event) => {
	console.log(this.state);
    }

    render () {
	return o("div", {style: {paddingLeft: "30px"}}, [
	    o(Subheader, "पशु राख्नुहोस्"),
	    o(SelectField, {
		floatingLabelText: "प्रकार",
		value: this.state.breed,
		onChange: e => this.setState({breed: e.target.value})
	    }, this.state.breeds.map(b => o(MenuItem, {value: b.id, primaryText: b.name, key: b.id}))),
	    o("br"),
	    o(TextField, {floatingLabelText: "ट्याग", onChange: e => this.setState({tag: e.target.value})}),
	    o("br"),
	    o(Checkbox, {label: "लैनो"}),
	    o("br"),
	    o(TextField, {floatingLabelText: "बिहानको दुध", onChange: e => this.setState({milk_morning: e.target.value})}),
	    o("br"),
	    o(TextField, {floatingLabelText: "बेलुकाको दुध", onChange: e => this.setState({milk_evening: e.target.value})}),
	    o("br"),
	    o(RaisedButton, {label: "पेश", primary: true, onClick: this.handleSubmit})
	]);
    }
}


class AnimalListItem extends React.Component {

    render () {
	return o(ListItem, {
	    primaryText: this.props.tag,
	    leftAvatar: o(Avatar, {src: this.props.avatar}),
	    secondaryText: `आम्दानी: ${this.props.money.statement}`
	});
    }

}


class AnimalListP extends React.Component {

    componentDidMount () {
	store.dispatch(fetchAnimals());
    }

    render () {
	return o("div", [
	    o(List, this.props.animals.map((animal) => o(AnimalListItem, animal))),
	    o(FloatingActionButton, {
		style: fabStyle,
		href: "/animals/add"
	    }, [o(ContentAdd)])
	]);
    }
}


export const AnimalList = connect(
    (state) => {
	return {
	    animals: state.animals.animals
	};
    }
)(AnimalListP);



