import React from "react";
import o from "react-hyperscript";
import { Link, Redirect } from "react-router-dom";
import Drawer from 'material-ui/Drawer';
import MenuItem from 'material-ui/MenuItem';
import Subheader from 'material-ui/Subheader';
import FontIcon from "material-ui/FontIcon";


const HomeIcon = o(FontIcon, {className: "material-icons"}, "home");
const AssessmentIcon = o(FontIcon, {className: "material-icons"}, "assessment");

const linkStyle = {
    textDecoration: "none"
};


export class LeftDrawer extends React.Component {

    render () {
	return o(Drawer, {
	    open: this.props.open,
	    docked: false, onRequestChange: this.props.change
	}, [
	    o(Subheader,"Basic"),
	    o(Link, {to: "/", style: linkStyle}, [o(MenuItem, {onClick: this.props.menuClick, leftIcon: HomeIcon}, "मुख्य पृष्ठ")]),
	    o(Link, {to: "/animals", style: linkStyle}, [o(MenuItem, {onClick: this.props.menuClick, leftIcon: AssessmentIcon}, "विवरण")]),
	    o(Link, {to: "/books", style: linkStyle}, [o(MenuItem, {onClick: this.props.menuClick, leftIcon: AssessmentIcon}, "आम्दानी-खर्च")]),
	]);
    }
}



