import axios from "axios";


export const ADD_ANIMAL = "ADD_ANIMAL";
export const addAnimal = (animal) => {
    return {
	type: ADD_ANIMAL,
	animal
    };
};


export const REQUEST_ANIMALS = "REQUEST_ANIMALS";
export const requestAnimals = () => {
    return {
	type: REQUEST_ANIMALS
    };
};


export const RECEIVE_ANIMALS = "RECEIVE_ANIMALS";
export const receiveAnimals = (response) => {
    return {
	type: RECEIVE_ANIMALS,
	animals: response.data,
	receivedAt: Date.now()
    };
};


export const FETCH_ANIMALS = "FETCH_ANIMALS";
export const fetchAnimals = () => {

    return (dispatch) => {
	dispatch(requestAnimals());

	return axios.get("/api/animal/")
	    .then(res => {
		dispatch(receiveAnimals(res));
	    });
    };
};



