import { combineReducers } from "redux";
import {
    ADD_ANIMAL,
    REQUEST_ANIMALS,
    RECEIVE_ANIMALS,
    addAnimal,
    requestAnimals,
    receiveAnimals,
} from "./actions.js";



const initialState = {
    animals: []
};


const animals = (state = {isFetching: false, animals: []}, action) => {
    switch (action.type) {
    case ADD_ANIMAL:
	return Object.assign({}, state, {
	    animals: [
		...state.animals,
		action.animal
	    ]
	});
    case REQUEST_ANIMALS:
	return Object.assign({}, state, {
	    isFetching: true
	});
    case RECEIVE_ANIMALS:
	return Object.assign({}, state, {
	    isFetching: false,
	    lastUpdated: action.receivedAt,
	    animals: state.animals.concat(action.animals)
	});
    default:
	return state;
    }
};



const animalApp = combineReducers({
    animals
});

export default animalApp;



