import React from "react";
import ReactDOM from "react-dom";
import {Provider} from "react-redux";
import o from "react-hyperscript";
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';

import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';

import { LeftDrawer } from "./sidebar";
import { AnimalList, AnimalEdit } from "./animal";
import { Books } from "./books";
import { Dashboard } from "./home";
import store from "./store";


class App extends React.Component {

    constructor(props) {
	super(props);
	this.state = {sidebar: false};
    }

    render () {
	return o("div", [
	    o(AppBar, {
		onLeftIconButtonClick: () => this.setState({sidebar: !this.state.sidebar}),
		title: "पशुपालन",
		iconClassNameRight: "muidocs-icon-navigation-expand-more"
	    }),
	    o("div", [
		o(Route, {exact: true, path: "/", component: Dashboard}),
		o(Route, {path: "/books", component: Books}),
		o(Route, {exact: true, path: "/animals", component: AnimalList}),
		o(Route, {path: "/animals/add", component: AnimalEdit}),
	    ]),
	    o(LeftDrawer, {
		open: this.state.sidebar,
		change: (sidebar) => this.setState({sidebar}),
		menuClick: () => this.setState({sidebar: false})
	    })
	]);
    }
}


ReactDOM.render(
    o(Provider, {store: store}, [
	o(MuiThemeProvider, [
	    o(Router, [o(App)])
	])
    ]),
    document.getElementById("root")
);



