import React from "react";


export const ADD_ANIMAL = "ADD_ANIMAL";
export const EDIT_ANIMAL = "EDIT_ANIMAL";


const animalAdder = (data) => {
    return {
	type: ADD_ANIMAL,
	data: data
    };
};


export const animals = (state = [], action) => {

    switch (action.type) {

    case ADD_ANIMAL:
	return [
	    ...state,
	    {
		tag: action.data.tag,
		breed: action.data.breed,
		is_lactating: action.data.is_lactating,
		milk_morning: action.data.milk_morning,
		milk_evening: action.data.milk_evening
	    }
	];

    case EDIT_ANIMAL:
	return state;

    default:
	return state;
    }
};



